// import Products from './components/Products/Products'
// import Navbar from './components/Navbar/Navbar'

import { useState, useEffect } from 'react'
import { Products, Navbar, Cart, Checkout, Footer } from './components'
import { commerce } from './lib/commerce'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'

const App = () => {
    const [products, setProducts] = useState([])
    const [cart, setCart] = useState({})

    const categoria = useState('all')
    const [categorias, setCategorias] = useState([])

    const sortTypes = [ { id: 0, label: 'Por ingreso', field:'created' }, { id: 1, label: 'Por nombre', field:'name' }, { id: 2, label: 'Por precio', field:'price' } ]
    const sortBy = useState(sortTypes[0])
    const sortReverse = useState(false)

    const fetchProducts = async () => {
        const { data } = await commerce.products.list()

        setProducts(data)
    }

    const fetchCart = async () => {
        setCart(await commerce.cart.retrieve())
    }

    const fetchCategories = async () => {
        const { data: categories } = await commerce.categories.list();
        
        setCategorias(categories)
    }

    const handleAddToCart = async (productId, quantity) => {
        const { cart } = await commerce.cart.add(productId, quantity)

        setCart(cart)
    }

    const handleUpdateCartQty = async (productId, quantity) => {
        const { cart } = await commerce.cart.update(productId, {quantity})

        setCart(cart)
    }

    const handleRemoveFromCart = async productId => {
        const { cart } = await commerce.cart.remove(productId)

        setCart(cart)
    }

    const handleEmptyCart = async () => {
        const { cart } = await commerce.cart.empty()

        setCart(cart)
    }

    useEffect(() => {
        fetchProducts()
        fetchCart()
        fetchCategories()
    }, [])

    // console.log(cart)

    return (
        <Router basename={ProcessingInstruction.env.PUBLIC_URL}>
            <div>
                <Navbar totalItems={cart.total_items} categorias={categorias} categoria={categoria} sortTypes={sortTypes} sortBy={sortBy} sortReverse={sortReverse}/>
                <Switch>
                    <Route exact path='/'>
                        <Products products={products} onAddToCart={handleAddToCart} categoria={categoria} sortBy={sortBy} sortReverse={sortReverse}/>
                    </Route>
                    <Route exact path='/cart'>
                        <Cart
                            cart={cart}
                            handleUpdateCartQty={handleUpdateCartQty}
                            handleRemoveFromCart={handleRemoveFromCart}
                            handleEmptyCart={handleEmptyCart}
                        />
                    </Route>
                    <Route exact path='/checkout'>
                        <Checkout cart={cart} />
                    </Route>
                </Switch>
                <Footer />
            </div>
        </Router>
    )
}

export default App
