import { AppBar, Toolbar, IconButton, Badge, MenuItem, Menu, Typography } from '@material-ui/core'
import { ShoppingCart } from '@material-ui/icons'
import { Link, useLocation } from 'react-router-dom'

import logo from '../../assets/commerce.png'
import CategoryIcon from '@material-ui/icons/Category'

import DropdownCat from './DropdownCat/DropdownCat'
import DropdownSort from './DropdownSort/DropdownSort'

import useStyles from './styles'

const Navbar = ({ totalItems, categorias, categoria, sortBy, sortTypes, sortReverse }) => {
    const classes = useStyles()
    const location = useLocation()

    return (
        <>
            <AppBar position="fixed" className={classes.appBar} color="inherit">
                <Toolbar>
                    <Typography component={Link} to='/' variant="h6" className={classes.title} color="inherit">
                        <img src={logo} alt="Commerce.js" height="25px" className={classes.image} />
                        TecnoShop
                    </Typography>
                    <div className={classes.grow}/>
                    {location.pathname === '/' && (
                        <div className={classes.button}>
                            <DropdownCat categorias={categorias} categoria={categoria}/>
                            <DropdownSort sortBy={sortBy} sortTypes={sortTypes} sortReverse={sortReverse} />
                            <IconButton component={Link} to='/cart' aria-label="Show cart items" color="inherit">
                                <Badge badgeContent={totalItems} color="secondary">
                                    <ShoppingCart />
                                </Badge>
                            </IconButton>
                        </div>
                    )}
                </Toolbar>
            </AppBar>
        </>
    )
}

export default Navbar
