import { Grid, Typography } from '@material-ui/core'

import Product from './Product/Product'
import useStyles from './styles'

const Products = ({ products, onAddToCart, categoria, sortBy : sort, sortReverse }) => {
    const [cat, setCat] = categoria
    const classes = useStyles()
    const [sortBy, setSortBy] = sort
    const [sortRev, setSortRev] = sortReverse

    const filtrar = objeto => (
        objeto.filter(product => (product.categories[0].id === cat) || cat === 'all')
    )

    const ordenar = objeto => {
            
        const compare = (a,b) => {
            const campo = sortBy.field
            var comparaA
            var comparaB

            if(campo==='price'){
                comparaA = a[campo]['raw']
                comparaB = b[campo]['raw']
            } else {
                comparaA = a[campo]
                comparaB = b[campo]
            }

            if(sortRev){
                const aux = comparaA
                comparaA = comparaB
                comparaB = aux
            }

            if(comparaA > comparaB){
                return 1
            } else if (comparaA < comparaB){
                return -1
            }
            return 0
        }

        return objeto.sort(compare)

    }

    if(products.length === 0) return <><div className={classes.toolbar}/><Typography variant="h6" align="center">Cargando...</Typography></>

    return (
        <main className={classes.content}>
            <div className={classes.toolbar}/>
                <Grid container justify="center" spacing={4}>
                    {ordenar(filtrar(products)).map(product => (
                        <Grid item key={product.id} xs={12} sm={6} md={4} lg={3} xl={2}>
                            <Product product={product} onAddToCart={onAddToCart} />
                        </Grid>
                    ))}
                </Grid>
        </main>
    )
}

export default Products