import { IconButton, Button, MenuItem, Menu, ClickAwayListener } from '@material-ui/core'
import { Link } from 'react-router-dom'
import SortIcon from '@material-ui/icons/Sort';
import { useState, useRef } from 'react'

const DropdownSort = ( {sortTypes, sortBy : ordenElegido, sortReverse} ) => {
    const [open, setOpen] = useState(false)
    const anchorRef = useRef(null);
    const [sortBy, setSortBy] = ordenElegido
    const [sortRev, setSortRev] = sortReverse

    const handleClose = e => {
        if (anchorRef.current && anchorRef.current.contains(e.target)) {
            return;
        }
      
        setOpen(false);
    }

    const handleClick = e => {
        if (anchorRef.current && !anchorRef.current.contains(e.target)) {
            // si ya estaba seleccionado, cambio el orden
            if(sortBy.id === e.target.value){
                setSortRev(!sortRev)
            }else{
                setSortRev(false)
            }
            console.log(sortRev)

            setSortBy( sortTypes.find( type => type.id === e.target.value) )
        }
        setOpen(!open)
    }

    return (
        <>
            <IconButton ref={anchorRef} aria-controls="menu" aria-haspopup="true" onClick={handleClick}><SortIcon /></IconButton>
            <ClickAwayListener onClickAway={handleClose}>
                <Menu
                id="menu"
                keepMounted
                anchorEl={anchorRef.current}
                open={open}
                getContentAnchorEl={null}
                anchorOrigin={{vertical: 'bottom', horizontal: 'center'}}
                transformOrigin={{vertical: 'top', horizontal: 'center'}}
                onClose= {handleClose}
                >
                    {sortTypes.map( type => (
                        <MenuItem value={type.id} key={type.id} selected={sortBy.id === type.id} onClick={handleClick}>{type.label}</MenuItem>
                    )
                    )}
                </Menu>
            </ClickAwayListener>
        </>
    )
}

export default DropdownSort
